@keyframes fadeIn {
    0% {
        opacity: 0.0;
    }
    50%{
        opacity: 0.4;
    }
    75%{
        opacity: 0.7;
    }
    100%{
        opacity: 1;
        height:50px;
    }
}

@keyframes fadeOut {
    50%{
        opacity: 0;
    }
    100%{
        height: 0px;
        opacity: 0;
        display: none;
    }
}

@keyframes fadeInForm {
    0% {
        opacity: 0.1;
        background-color: green;
    }
    50%{
        opacity: 0.6;
        background-color: #7400B8;
    }
    75%{
        opacity: 0.8;
        background-color:#7400B8;
    }
    100%{
        opacity: 1;
        background-color: #7400B8;
    }
}

@media screen and (min-width: 200px){
    .container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100vh;
        margin: auto;
        max-width: 80%;
    }
    
    .done{
        text-decoration: line-through;
    }
    
    ul{
        list-style: none;
        margin: 0;
        padding: 0;
    }
    
    .title{
        color: #7400B8;
        font-size: 1.5em;
    }
    
    .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        /* width: 100%; */
        font-size: 1.1em;
        margin-top: 10px;
        padding: 10px;
    }
    
    .descriptionCard{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 5px 10px;
        border-radius: 0px 0px 5px 5px;
        background-color: green;
        opacity: 0;
        height: 0px;
        word-wrap: break-word;
        animation: fadeIn 0.5s forwards ease-out;
    }
    
    .fadeOut{
        opacity: 1;
        height: 50px;
        animation: fadeOut 0.5s forwards ease-out;
    }
    
    .descriptionCardHide{
        display: none;
    }
    
    .card{
        display: flex;
        flex-direction: column;
        margin-top: 10px;
        box-shadow: 0 1px 4px 1px rgba(0,0,0,0.2);
        background-color: #7400B8;
        color: white;
        border-radius: 7px;
        transition: all 0.3s ease-out;
    }
    
    .card:hover{
        transform: scale(1.01);
    }
    
    .header{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    .addButton{
        width: 40px;
        height: 40px;
        font-size: 20px;
        border-style: none;
        background-color: green;
        cursor: pointer;
        color: white;
        border-radius: 50%;
        transition: all 0.3s ease-out;
    }
    
    .addButton:hover{
        transform: scale(1.1);
    }
    
    .imgStyle{
        border-style: none;
        background-color: #7400B8;
        cursor: pointer;
        transition: all 0.3s ease-out;
    }
    
    .imgStyle:hover{
        transform: scale(1.07);
    }
    
    .imgStyle img{
        width: 25px;
    }
    
    .modal{
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.75);
    }
    
    .cardModal{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 70%;
        height: 320px;
        padding: 20px;
        background-color: #7400B8;
        opacity: 0;
        animation: fadeInForm 0.9s forwards ease-out;
    }
    
    .cardModal form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin-top: 10px;
    }
    
    .input-text{
        width: 95%;
        height: 40px;
        padding: 10px;
        background-color: #EDE8E8;
        border: none;
        box-shadow: 0px 4px 4px rgb(0 0 0 / 10%);
        border-radius: 7px;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
        font-size: 1em;
    }
    
    .input-area{
        width: 95%;
        height: 100px;
        padding: 10px;
        background-color: #EDE8E8;
        border: none;
        box-shadow: 0px 4px 4px rgb(0 0 0 / 10%);
        border-radius: 7px;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
        font-size: 1em;
    }
    
    .btn-add{
        width: 80px;
        height: 40px;
        padding: 10px;
        background-color: #EDE8E8;
        border: none;
        box-shadow: 0px 4px 4px rgb(0 0 0 / 10%);
        border-radius: 7px;
        transition: all 0.3s ease-out;
        background-color: green;
        font-size: 1em;
        font-weight: 700;
        color: white;
    }
    
    .btn-add:hover{
        transform: scale(1.05);
    }
    
    .cardModal form input, textarea{
        margin-bottom: 10px;
    }
    
    .cardModal form textarea{
        height: 150px;
    }
    
    .modalHide{
        display: none;
    }
    
    #delete{
        z-index: 200;
    }

    .credits, .creditsWhite{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    margin-bottom: 10px;
    color: #7400B8;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 0.8em;
    }

    .creditsWhite{
        color: white;
    }
}

@media screen and (min-width: 500px){

    .container{
        max-width: 60%;
    }

    .item{
        font-size: 1.5em;
    }
    .imgStyle img {
        width: 30px;
    }
    .title{
        font-size: 2.5em;
    }

    .addButton{
        width: 50px;
        height: 50px;
    }

    .cardModal {
        width: 40%;
    }

    .credits, .creditsWhite{
        font-size: 1em;
    }

}